<template>
	<div class="itemWrap" @click="changeImg()">
		<span v-show="bol"><slot name="normalImg"> </slot><br/></span>
		<span v-show="!bol"><slot name="activeImg"> </slot><br/></span>
		<span v-text="txt" :class="{ac:!bol}"></span>
	</div>
</template>


<script>
	export default {
		props:['txt','id'],
		computed:{
			bol:function(){
				if(this.id==this.$parent.sel){
					return false
				}
				return true
			}
		},
		methods:{
			changeImg:function(){
				this.$emit('change',this.id);
				this.$router.push('/'+this.id)
			}
		}
	}
</script>

<style>
	.itemWrap{float: left;width: 20%;padding-top: 11px}
	.icon{width: 30px;}
	span{font-size: 12px;color: #999;}
	.ac{color: #42bd56;}
</style>


