<template>
	<div>
		<my-header>
			<span  class="title">书影音</span>
			<img src="../../assets/images/ic_group_search.png" alt="" class="radio_search">
			<img src="../../assets/images/ic_chat_green.png" alt="" class="radio_chat"> 
		</my-header>
		<ul class="radio_title">
			<li @click="changeCol(1)" :class="{border_decoration:selected==1}"><router-link to="/radio/film">电影</router-link></li>
			<li @click="changeCol(2)" :class="{border_decoration:selected==2}"><router-link to="/radio/book" >读书</router-link></li>
			<li @click="changeCol(3)" :class="{border_decoration:selected==3}"><router-link to="/radio/television" >电视</router-link></li>
			<li @click="changeCol(4)" :class="{border_decoration:selected==4}"><router-link to="/radio/city">同城</router-link></li>
			<li @click="changeCol(5)" :class="{border_decoration:selected==5}"><router-link to="/radio/misic">音乐</router-link></li>
		</ul>
		<router-view></router-view>
	</div>
</template>

<script>
	import MyHeader from '../../components/header'
	export default{
		components:{
			MyHeader
		},
		props:['mark'],
		data (){
			return{
				selected:1,
				bol:true,

			}
		},
		methods:{
			changeCol:function(n){
				this.selected = n;
			}
		}
	}
</script>

<style>
	.radio_title{
		width: 100%;height: 30px;font-size: 14px;position: fixed;left:0;top:70px;right: 0;background: #fff;z-index: 90
	}
	.radio_title a{text-decoration: none;}

	.radio_title li a{
		line-height: 30px;width: 20%;color: gray;float: left;
		
	}
	.radio_title .border_decoration a{color: #42bd56;border-bottom: 1px solid #42bd56;}
</style>


