<template>
	<div class="header" :style="{background:col}">
		<slot></slot>
		<slot name="leftImg"></slot>
		<slot name="rightImg"></slot>

	</div>
</template>

<script>
	export default {
		props:{
			col:{
				default:"#f7f7f7"
			}
		}
	}
</script>

<style>
	.header{width: 100%;height: 70px;position: fixed;left: 0;top:0;z-index: 100}
	.title{font-size: 16px;line-height: 70px;color: #666;}
	.search{width: 30px;position: absolute;left:18px;top:24px;z-index: 10}
	.searchinput{width: 300px;height: 40px;border: none;position: absolute;left:10px;top:18px;text-indent: 3em;z-index: 1}
	.scan{width: 25px;position: absolute;left: 280px;top:26px;z-index: 10}
	.chat{width: 35px;position: absolute;left: 325px;top:20px;}	
	.radio_search,.news_search{width:35px;position: absolute;left:260px;top:20px;}
	.radio_chat,.news_chat,.mine_chat{width: 35px;position: absolute;left:320px;top:20px;}
</style>


