<template>
	<div class="mine">
		<my-header>
			<span  class="title">我的</span>
			<img src="../../assets/images/ic_group_chat_setting.png" alt="" class="mine_chat"> 
		</my-header>
		<div class="mine_banner">
			<img src="../../assets/images/main_img.png" class="banner_img">
			<div class="unfinish" v-show="showing">
				<img src="../../assets/images/ic_share_default_user.png" class="head">
				<a href="javascript:return false;"  class="login" @click="loginFn">登录/注册</a>
			</div>
			<div class="finish" v-show="!showing">
				<img src="../../assets/images/ic_share_default_user.png" class="head2">
				<p class="name">{{ msg }}</p>
				<p class="small_icon">5221关注</p>
			</div>
			<div class="loginBox" v-show="bol">
				<h3>欢迎来到豆瓣</h3>
				<span>用户名</span><input type="text" placeholder="手机号/邮箱" v-model="msg"><br/>
				<span>密&nbsp;&nbsp;&nbsp;&nbsp;码</span><input type="password" placeholder="密码"><br/>
				<button type="button" @click="Logining">登录</button>
				<i class="closeicon" @click="closeLogin">X</i>
			</div>
		</div>
		<div class="mine_mid">
			<div class="mine_mid_inner">
				<img src="../../assets/images/ic_mine_notification.png" alt="">
				<p>提醒</p>
				<i>></i>
			</div>
			<div class="mine_mid_bottom">
				<p>暂无数据</p>
			</div>
		</div>
		<div class="mine_bottom_list">
			<ul>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_likes.png" alt=""></a>
					<p>喜欢</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_note.png" alt=""></a>
					<p>日记</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_album.png" alt=""></a>
					<p>相册</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_status.png" alt=""></a>
					<p>我的广播</p>
				</li>
			</ul>
			<ul>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_movies_tvs.png" alt=""></a>
					<p>电影.电视</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_books.png" alt=""></a>
					<p>读书</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_music.png" alt=""></a>
					<p>音乐</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_events.png" alt=""></a>
					<p>同城活动</p>
				</li>
			</ul>
			<ul>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_movies_tvs.png" alt=""></a>
					<p>豆瓣时间</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_doulist.png" alt=""></a>
					<p>豆列</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_orders.png" alt=""></a>
					<p>订单</p>
				</li>
				<li>
					<a href="##"><img src="../../assets/images/ic_my_wallet.png" alt=""></a>
					<p>钱包</p>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import MyHeader from '../../components/header'
	export default{
		components:{
			MyHeader
		},
		data:function(){
			return{
				bol:false,
				showing:true,
				msg:""
			}
		},
		methods:{
			loginFn:function(){
				this.bol=true;
			},
			closeLogin:function(){
				this.bol=false;
			},
			Logining:function(){
				this.showing=false
				this.bol=false;
			}
		}
	}
</script>

<style>
	.mine{position: relative;}
	.mine_banner{width: 100%;height: 120px;position: relative;margin-top: 70px;}
	.banner_img{width: 100%;height: 120px;}
	.head{width: 60px;position: absolute;left: 50px;top: 30px;}
	.login{position: absolute;width: 180px;left:130px;top:40px;height: 30px;border: 1px solid #fff;color:#fff;text-align: center;line-height: 30px;letter-spacing:3px;text-decoration: none;}
	.mine_mid{width: 100%;border-top: 10px solid #f7f7f7;}
	.mine_mid_inner{position: relative;padding:10px 20px;border-bottom: 1px solid #f7f7f7;height: 15px;}
	.mine_mid_inner img{width: 20px;float: left;}
	.mine_mid_inner p{float: left;color: gray;font-size: 12px;padding-left: 10px;}
	.mine_mid_inner i{float: right;color: gray;}
	.mine_mid_bottom{text-align: center;height: 30px;}
	.mine_mid_bottom p{color: 12px;color: #ddd;font-size: 12px;height: 30px;line-height: 30px;}
	.mine_bottom_list{width: 100%;border-top: 10px solid #f7f7f7;padding-left:0 5px;}
	.mine_bottom_list li{width: 25%;color: #666;font-size: 12px;float: left;padding-top: 20px;}
	.mine_bottom_list li img{width: 25px;}
	.mine_bottom_list ul{border-bottom: 1px solid #f7f7f7;height: 90px;}
	.loginBox{position: absolute;left: 0;top: 200px;margin:0 auto;background:rgba(0,0,0,.3);width: 100%;text-align: center;z-index: 100}
	.loginBox input{height: 20px;text-indent: 1em;width: 200px;margin-left: 20px;margin-top: 10px;}
	.loginBox button{padding: 8px 20px ; border:none;background: #42bd56;border-radius: 10px;margin-top: 10px;margin-bottom: 10px;color: #fff;font-size: 16px;}
	.loginBox span{font-size: 16px;color: #fff;}
	.closeicon{position: absolute;right: 10px;top:10px;color: #42bd56;font-style:normal;cursor: pointer;font-size: 14px;}
	.loginBox h3{padding: 20px 0;color: #fff;}
	.finish{position: absolute;left: 0;top:0;}
	.head2{width: 60px;position: absolute;left: 20px;top: 30px;}
	.name{position: absolute;top:35px;left: 100px;font-size: 18px;color: #666;width: 200px;text-align: left;}
	.small_icon{position: absolute;top: 67px;left: 100px;font-size: 12px;color: #999;width: 100px;text-align: left;}
</style>


