<template>
	<div>
		<my-header>
			<span  class="title">广播</span>
			<img src="../../assets/images/ic_group_chat_share.png" alt="" class="news_search">
			<img src="../../assets/images/ic_chat_green.png" alt="" class="news_chat"> 
		</my-header>

		<div class="news_banner">
			<div class="news_banner_top">
				<img src="../../assets/images/ic_status_anonymous_header.png" alt="">
				<h4>发现有趣的人</h4>
				<p>关注他们，发现更大的世界</p>
			</div>
			<div class="news_banner_list">
				<ul>
					<li v-for="(item,index) in getdataarr2">
						<img :src="item.src" class="posi_img">
						<p>{{ item.name }}<br/><span>6346人关注</span></p>
						<a href="##">关注</a>
						<p>{{item.des}}</p>
						<img :src="item.url" class="news_img">
						<em><img src="../../assets/images/ic_like_full.png" alt=""><i>20</i></em>
						<em><img src="../../assets/images/ic_comment_full.png" alt=""><i>2</i></em>
						<i class="close_icon" @click="closeFn(index)">X</i>
					</li>
				</ul>
			</div>
		</div>
		
	</div>
</template>

<script>
	import MyHeader from '../../components/header'
	export default{
		components:{
			MyHeader
		},
		data (){
			return {
				getdataarr2:[
					{name:"蒲扇",des:"突然发神经做了去年理综题#有毒#",url:require("../../assets/images/groupimg1.png"),src:require("../../assets/images/new_art1.jpg")},
					{name:"默",des:"建了自己的小小微信公众号：mosneverland 欢迎来关注",url:require("../../assets/images/groupimg2.png"),src:require("../../assets/images/new_art2.jpg")},
					{name:"四月",des:"#壁纸#Live your life",url:require("../../assets/images/groupimg3.png"),src:require("../../assets/images/new_art3.jpg")},
					{name:"宋小君",des:"八卦界亡了，自媒体人多不容易。坚决拥护社会主义核心价值观。",url:require("../../assets/images/news_list_img19.jpg"),src:require("../../assets/images/new_art4.jpg")}

				],
			}
		},
		methods:{
			closeFn:function(i){
				console.log(i)
				this.getdataarr2.splice(i,1)
			}
		}
		
	}
</script>
<style>
	.news_banner{width: 100%;background: #f7f7f7;text-align: left;margin-top: 70px;padding-bottom: 80px;}
	.news_banner_top{background: #fff;width: 100%;padding:20px 20px;height: 100px;}
	.news_banner_top img{width: 120px;float: left;padding-right: 20px;}
	.news_banner_top h4{padding: 10px ;font-size: 16px;color: #666;}
	.news_banner_top p{font-size: 12px;color: gray;}
	.news_banner_list li{margin: 20px 20px;background: #fff;border-radius: 10px;box-shadow: 3px 3px 14px #888888;padding:30px 60px; }
	.posi_img{width: 40px;border-radius: 50%;position: absolute;left:10px;top:25px;}
	.news_banner_list a{position: absolute;right: 25px;top:25px;height: 30px;width: 60px;background: #42bd56;font-size: 12px;text-align: center;line-height: 30px;border-radius: 5px;color: #fff;text-decoration: none;}
	.news_img{width: 200px;height: 100px;}
	em img{width: 15px;}
	li{position: relative;}
	em i{font-size: 12px;color: gray;font-style: normal;padding-left: 5px;}
	.news_banner_list p{color: #666;font-size: 16px;padding-bottom: 5px;}
	.close_icon{position: absolute;right: 10px;top:2px;color: lightgray;font-size: 18px;font-style: normal;}
</style>


